<template>
    <div>
        <div class="breadcrumb">
            <el-row justify="center" type="flex">
                <el-col :span="1">
                </el-col>
                <el-col :span="22" style="width:1438px">
                    <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item :to="{ path: '/home' }">Home</el-breadcrumb-item>
                        <el-breadcrumb-item :to="{ path: '/News' }">News</el-breadcrumb-item>
                        <el-breadcrumb-item :to="{ path: '/News/torsion-spring' }">Torsion-spring</el-breadcrumb-item>
                    </el-breadcrumb>
                </el-col>
                <el-col :span="1">
                </el-col>
            </el-row>
        </div>

        <el-row justify="center" type="flex" style="background:#E6A23C;height: 80px;">
            <el-col :span="1">
            </el-col>
            <el-col :span="22" style="width:1438px">
                <h1 style="color:#F2F6FC;text-align: left;">News</h1>
            </el-col>
            <el-col :span="1">
            </el-col>
        </el-row>

        <div class="torsionimg">
            <el-row justify="center" type="flex">
                <el-col :span="1">
                </el-col>
                <el-col :span="22" style="width:1438px">
                    <h1>Torsion Spring.</h1>
                    <p>
                        A torsion spring is a spring that works by torsion or twisting. Mechanical energy is created when it
                        is twisted. When it is twisted, it exerts a force (torque) in the opposite direction, proportional
                        to the amount (angle) it is twisted. A torsion bar is a straight bar of metal that is subjected to
                        twisting (shear stress) about its axis by torque applied at its ends.
                    </p>
                    <p>
                        Heavy duty torsion springs (single or double) are another DVT Spring Manufacturing specialty, and
                        are utilized in various technical instruments as well as many types of machinery and equipment.
                    </p>
                    <p>
                        Torsion springs mainly play a balancing role in industrial production. For example, in a car’s
                        suspension system, which interacts with the car’s shock absorbers, the torsion angle of the spring
                        deforms the material and returns it to its original state. Thereby preventing the car from shaking
                        too much, which plays a good role in protecting the safety system of the car. However, the spring
                        will break and fail during the entire protection process, which is called fatigue fracture, so
                        technicians or consumers should pay attention to fatigue fracture. As a technician, we should do our
                        best to avoid sharp corners, notches, and sudden changes in section in the structural design of
                        parts, thereby reducing fatigue cracks caused by stress concentrations. Therefore, Spring
                        manufacturers should improve the machining quality of the surface of the torsion springs to reduce
                        the source of fatigue. In addition, surface strengthening treatment can also be used for different
                        torsion spring.
                    </p>
                    <p>
                        <img class="img" v-lazy="TorsionSpring02" alt="" />
                    </p>
                    <p>
                        The type of mechanical torsion spring you would typically use is known as a helical torsion spring.
                        This is a metal wire twisted into a helix, or coil shape, using sideways forces to twist the wire
                        around its axis, as opposed to using shear stress, as in the torsion bar.
                    </p>
                    <p>
                        DVT Spring has over seventeen years of experience manufacturing the highest-quality torsion springs.
                        If you need torsion springs, or are looking for torsion spring replacements, there’s only one
                        company to call!
                    </p>
                    <p>
                        <img class="img" v-lazy="TorsionSpring03" alt="" />
                    </p>
                </el-col>
                <el-col :span="1">
                </el-col>
            </el-row>
        </div>

